import * as Actions from './action-types';
/**
 * Generate a move to be dispatched to the game move reducer.
 *
 * @param {string} type - The move type.
 * @param {Array}  args - Additional arguments.
 * @param {string}  playerID - The ID of the player making this action.
 * @param {string}  credentials - (optional) The credentials for the player making this action.
 */

export var makeMove = function makeMove(type, args, playerID, credentials) {
  return {
    type: Actions.MAKE_MOVE,
    payload: {
      type: type,
      args: args,
      playerID: playerID,
      credentials: credentials
    }
  };
};
/**
 * Generate a game event to be dispatched to the flow reducer.
 *
 * @param {string} type - The event type.
 * @param {Array}  args - Additional arguments.
 * @param {string}  playerID - The ID of the player making this action.
 * @param {string}  credentials - (optional) The credentials for the player making this action.
 */

export var gameEvent = function gameEvent(type, args, playerID, credentials) {
  return {
    type: Actions.GAME_EVENT,
    payload: {
      type: type,
      args: args,
      playerID: playerID,
      credentials: credentials
    }
  };
};
/**
 * Generate an automatic game event that is a side-effect of a move.
 * @param {string} type - The event type.
 * @param {Array}  args - Additional arguments.
 * @param {string}  playerID - The ID of the player making this action.
 * @param {string}  credentials - (optional) The credentials for the player making this action.
 */

export var automaticGameEvent = function automaticGameEvent(type, args, playerID, credentials) {
  return {
    type: Actions.GAME_EVENT,
    payload: {
      type: type,
      args: args,
      playerID: playerID,
      credentials: credentials
    },
    automatic: true
  };
};
/**
 * Used to reset the Redux store's state on a sync.
 * @param {object} state - The state to restore.
 * @param {Array} log - The log to restore.
 */

export var sync = function sync(state, log) {
  return {
    type: Actions.SYNC,
    state: state,
    log: log,
    clientOnly: true
  };
};
/**
 * Used to update the Redux store's state in response to
 * an action coming from another player.
 * @param {object} state - The state to restore.
 * @param {Array} deltalog - A log delta.
 */

export var update = function update(state, deltalog) {
  return {
    type: Actions.UPDATE,
    state: state,
    deltalog: deltalog,
    clientOnly: true
  };
};
/**
 * Used to reset the game state.
 * @param {object} state - The initial state.
 */

export var reset = function reset(state) {
  return {
    type: Actions.RESET,
    state: state,
    clientOnly: true
  };
};
/**
 * Used to undo the last move.
 */

export var undo = function undo() {
  return {
    type: Actions.UNDO
  };
};
/**
 * Used to redo the last undone move.
 */

export var redo = function redo() {
  return {
    type: Actions.REDO
  };
};